#o-result{
    position: relative;
    display:flex;
    max-width:1300px;
    margin: 0 auto;

    .o-content-container{
        text-align: center;
    }

    .o-leftside-img{
        display: none;
        width:350px;
        min-height:500px;
        background-image: url(https://orsted.dk/-/media/WWW/Assets/DCS/projects/el-tjek/static/media/result);    
        background-size: cover;
        background-position: center center;
    }

    .o-rightside-content{
        position: relative;
        padding: 40px 70px 70px 70px;
        width: 100%;
        text-align:center;
        overflow: hidden;
    }

    .o-headline1{
        margin-bottom: 8px;
    }


    .o-tips-container{
        display: flex;
        justify-content: left;
        align-items: flex-start;
        flex-wrap: wrap;
        margin-top:40px;
        padding: 35px 25px;
        background-color: $green;
    }

    .o-tip{
        width:50%;
        padding: 15px 25px;
        text-align: center;

        .o-tip-img{
            margin-top: 30px;
        }

        .o-tip-title{
            font-family: $font-bold;
            margin-top:20px;
        }
    }


    .o-comparison{
        margin-top:40px;
        border: 1px solid $sand;
        padding: 35px 25px;

        .o-comparison-top{
            display: flex;
            justify-content: center;
            align-items: center;
            
            
            .o-comparison-you-box{
                display: inline-block;
                width:20px;
                height:20px;
                background-color: $blue;
                margin-right:10px;
            }

            .o-comparison-average-box{
                display: inline-block;
                width:20px;
                height:20px;
                background-color: $sand;
                margin:0 10px 0 60px;
            }
        }

        .o-comparison-boxes{
            display: flex;
            justify-content: center;
            align-items: flex-start;
            flex-wrap: wrap;
            width: 100%;

            .o-comparison-box{
                text-align: center;
                width:50%;
                padding:8% 3%;

                .o-label{
                    font-family: $font-bold;
                    margin-top:5px;
                }

                .o-comparison-bars{
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    flex-direction: column;
                    width:100%;

                    .o-comparison-bar{
                        padding:8% 0 0 0;
                        display: flex;
                        width:100%;
                        justify-content: center;
                        align-items: center;
                        
                        .o-comparison-bar-label{
                            width:50%;
                            text-align: left;
                        }

                        .o-comparison-bars-container{
                            position:relative;
                            width:50%;
                            height:25px;

                            .o-comparison-tooltip-container{
                                position: relative;
                                text-align: center;
                            }

                            .o-comparison-tooltip{
                                position: absolute;
                                bottom:10px;
                                border:1px solid $black;
                                padding: 10px 15px;
                                background-color: white;
                                z-index: 10;
                                font-size: 14px;
                                font-family: $font-bold;
                                box-shadow: 0 0 20px 0 rgba(0,0,0,0.25);
                            }
                            .o-comparison-tooltip:after, .o-comparison-tooltip:before {
                                top: 100%;
                                left: 15px;
                                border: solid transparent;
                                content: " ";
                                height: 0;
                                width: 0;
                                position: absolute;
                                pointer-events: none;
                            }

                            .o-comparison-tooltip:after {
                                border-color: rgba(255, 255, 255, 0);
                                border-top-color: #ffffff;
                                border-width: 6px;
                                margin-left: -6px;
                            }
                            .o-comparison-tooltip:before {
                                border-color: rgba(59, 73, 86, 0);
                                border-top-color: #3b4956;
                                border-width: 7px;
                                margin-left: -7px;
                            }

                            .o-comparison-bar-you{
                                position: absolute;
                                top:0;
                                left:0;
                                height:15px;
                                background-color: $blue;
                                border-radius: 0 50px 50px 0;
                                z-index: 2;
                                cursor: pointer;
                                transition:all 200ms ease-out;

                            }
                            .o-comparison-bar-average{
                                position: absolute;
                                top:10px;
                                left:0;
                                height:15px;
                                background-color: $sand;
                                border-radius: 0 50px 50px 0;
                                z-index: 0;
                                cursor: pointer;
                                transition:all 300ms ease-out;
                            }

                            .o-comparison-tooltip-you{
                                color:$blue
                            }

                            .o-comparison-tooltip-average{
                                color:$dark-sand;
                            }
                        }
                    }
                }
            }
        }
    }


    .o-pdf-download{
        position: relative;
        display: inline-block;
        margin-top:40px;
    }


    .o-orstedshop{
        display: flex;
        align-items: flex-end;
        margin-top:60px;
        position: relative;
        width:100%;
        height:250px;
        border: 1px solid $sand;

        .o-orstedshop-img{
            width: 50%;
            height:100%;
            background-image: url(https://orsted.dk/-/media/WWW/Assets/DCS/projects/el-tjek/static/media/philipshue);
            background-size: cover;
            background-position: right center;
        }
        
        .o-orstedshop-txt{
            width:50%;
            text-align: left;
            padding:30px;
            .o-btn{
                margin-top:12px;
                display:inline-block;
            }
        }
    }

    .o-usage-negative{
        color:$red;
    }
    .o-usage-positive{
        color:$green;
    }
}